.jvectormap-container {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
}

.jvectormap-tip {
    position: absolute;
    display: none;
    border: solid 1px #cdcdcd;
    border-radius: 3px;
    background: #292929;
    color: white;
    font-family: sans-serif, Verdana;
    font-size: smaller;
    padding: 3px;
}

.jvectormap-zoomin,
.jvectormap-zoomout,
.jvectormap-goback {
    position: absolute;
    inset-inline-start: 10px;
    border-radius: 3px;
    background: #f8f8f9;
    padding: 5px;
    color: #000;
    cursor: pointer;
    line-height: 20px;
    text-align: center;
    box-sizing: content-box;
}

.jvectormap-zoomin,
.jvectormap-zoomout {
    width: 20px;
    height: 20px;
}

.jvectormap-zoomin {
    inset-block-start: 10px;
}

.jvectormap-zoomout {
    inset-block-start: 50px;
}

.jvectormap-goback {
    inset-block-end: 10px;
    z-index: 1000;
    padding: 6px;
}

.jvectormap-spinner {
    position: absolute;
    inset-inline-start: 0;
    inset-block-start: 0;
    inset-inline-end: 0;
    inset-block-end: 0;
    background: center no-repeat
        url(data:image/gif;base64,);
}

.jvectormap-legend-title {
    font-weight: bold;
    font-size: 14px;
    text-align: center;
}

.jvectormap-legend-cnt {
    position: absolute;
}

.jvectormap-legend-cnt-h {
    inset-block-end: 0;
    inset-inline-end: 0;
}

.jvectormap-legend-cnt-v {
    inset-block-start: 0;
    inset-inline-end: 0;
}

.jvectormap-legend {
    background: black;
    color: white;
    border-radius: 3px;
}

.jvectormap-legend-cnt-h .jvectormap-legend {
    float: $float-left;
    margin: 0 10px 10px 0;
    padding: 3px 3px 1px 3px;

    .jvectormap-legend-tick {
        float: $float-left;
    }
}

.jvectormap-legend-cnt-v .jvectormap-legend {
    margin: 10px 10px 0 0;
    padding: 3px;
}

.jvectormap-legend-cnt-h {
    .jvectormap-legend-tick {
        width: 40px;
    }

    .jvectormap-legend-tick-sample {
        height: 15px;
    }
}

.jvectormap-legend-cnt-v .jvectormap-legend-tick-sample {
    height: 20px;
    width: 20px;
    display: inline-block;
    vertical-align: middle;
}

.jvectormap-legend-tick-text {
    font-size: 12px;
}

.jvectormap-legend-cnt-h .jvectormap-legend-tick-text {
    text-align: center;
}

.jvectormap-legend-cnt-v .jvectormap-legend-tick-text {
    display: inline-block;
    vertical-align: middle;
    line-height: 20px;
    padding-inline-start: 3px;
}

#world-map-markers1 {
    .jvectormap-zoomin,
    .jvectormap-zoomout {
        display: none;
    }
}
