.ql-wrapper {
    border-radius: 7px;
}

.ql-container {
    box-sizing: border-box;
    font-family: Helvetica, Arial, sans-serif;
    font-size: 13px;
    height: 100%;
    margin: 0px;
    position: relative;
    border-radius: 0 0 7px 7px;

    &.ql-disabled {
        .ql-tooltip {
            visibility: hidden;
        }

        .ql-editor ul[data-checked] > li::before {
            pointer-events: none;
        }
    }
}

.ql-clipboard {
    inset-inline-start: -100000px;
    height: 1px;
    overflow-y: hidden;
    position: absolute;
    inset-block-start: 50%;

    p {
        margin: 0;
        padding: 0;
    }
}

