    box-sizing: border-box;

    * {
        box-sizing: border-box;
    }

    .ql-hidden {
        display: none;
    }

    .ql-out-bottom,
    .ql-out-top {
        visibility: hidden;
    }

    .ql-tooltip {
        position: absolute;
        transform: translateY(10px);

        a {
            cursor: pointer;
            text-decoration: none;
        }

        &.ql-flip {
            transform: translateY(-10px);
        }
    }

    .ql-formats {
        display: inline-block;
        vertical-align: middle;
        color: #858398;
        background: #f8f8ff;

        &:after {
            clear: both;
            content: "";
            display: table;
        }
    }

    .ql-stroke {
        fill: none;
        stroke: #495057;
        stroke-linecap: round;
        stroke-linejoin: round;
        stroke-width: 2;
    }

    .ql-stroke-miter {
        fill: none;
        stroke: #444;
        stroke-miterlimit: 10;
        stroke-width: 2;
    }

    .ql-fill,
    .ql-stroke.ql-fill {
        fill: #444;
    }

    .ql-empty {
        fill: none;
    }

    .ql-even {
        fill-rule: evenodd;
    }

    .ql-thin,
    .ql-stroke.ql-thin {
        stroke-width: 1;
    }

    .ql-transparent {
        opacity: 0.4;
    }

    .ql-direction {
        svg:last-child {
            display: none;
        }

        &.ql-active svg {
            &:last-child {
                display: inline;
            }

            &:first-child {
                display: none;
            }
        }
    }

    .ql-editor {
        h1 {
            font-size: 2em;
        }

        h2 {
            font-size: 1.5em;
        }

        h3 {
            font-size: 1.17em;
        }

        h4 {
            font-size: 1em;
        }

        h5 {
            font-size: 0.83em;
        }

        h6 {
            font-size: 0.67em;
        }

        a {
            text-decoration: underline;
        }

        blockquote {
            border-inline-start: 4px solid #e7eaf3;
            margin-block-end: 5px;
            margin-block-start: 5px;
            padding-inline-start: 16px;
        }

        code {
            background-color: #f0f0f0;
            border-radius: 3px;
        }

        pre {
            background-color: #f0f0f0;
            border-radius: 3px;
            white-space: pre-wrap;
            margin-block-end: 5px;
            margin-block-start: 5px;
            padding: 5px 10px;
        }

        code {
            font-size: 85%;
            padding: 2px 4px;
        }

        pre.ql-syntax {
            background-color: #23241f;
            color: #f8f8f2;
            overflow: visible;
        }

        img {
            max-width: 100%;
        }
    }

    .ql-picker {
        /* color: #444; */
        display: inline-block;
        // float: $float-left;
        font-size: 14px;
        font-weight: 500;
        position: relative;
        vertical-align: middle;
    }

    .ql-picker-label {
        cursor: pointer;
        display: inline-block;
        height: 100%;
        padding-inline-start: 8px;
        padding-inline-end: 2px;
        position: relative;
        width: 100%;

        &::before {
            display: inline-block;
            line-height: 22px;
        }
    }

    .ql-picker-options {
        background-color: #fff;
        display: none;
        min-width: 100%;
        padding: 4px 8px;
        position: absolute;
        white-space: nowrap;

        .ql-picker-item {
            cursor: pointer;
            display: block;
            padding-block-end: 5px;
            padding-block-start: 5px;
        }
    }

    .ql-picker.ql-expanded {
        .ql-picker-label {
            color: #e7eaf3;
            z-index: 2;

            .ql-fill {
                fill: #e7eaf3;
            }

            .ql-stroke {
                stroke: #e7eaf3;
            }
        }

        .ql-picker-options {
            display: block;
            margin-block-start: -1px;
            inset-block-start: 100%;
            z-index: 1;
        }
    }

