    .ql-picker {
        &:not(.ql-color-picker):not(.ql-icon-picker) svg {
            position: absolute;
            margin-block-start: -9px;
            inset-inline-end: 6px;
            inset-block-start: 50%;
            width: 18px;
        }

        &.ql-header .ql-picker-label[data-label]:not([data-label=""])::before,
        &.ql-font .ql-picker-label[data-label]:not([data-label=""])::before,
        &.ql-size .ql-picker-label[data-label]:not([data-label=""])::before,
        &.ql-header .ql-picker-item[data-label]:not([data-label=""])::before,
        &.ql-font .ql-picker-item[data-label]:not([data-label=""])::before,
        &.ql-size .ql-picker-item[data-label]:not([data-label=""])::before {
            content: attr(data-label);
        }

        &.ql-header {
            width: 98px;

            .ql-picker-label::before,
            .ql-picker-item::before {
                content: "Normal";
            }

            .ql-picker-label[data-value="1"]::before,
            .ql-picker-item[data-value="1"]::before {
                content: "Heading 1";
            }

            .ql-picker-label[data-value="2"]::before,
            .ql-picker-item[data-value="2"]::before {
                content: "Heading 2";
            }

            .ql-picker-label[data-value="3"]::before,
            .ql-picker-item[data-value="3"]::before {
                content: "Heading 3";
            }

            .ql-picker-label[data-value="4"]::before,
            .ql-picker-item[data-value="4"]::before {
                content: "Heading 4";
            }

            .ql-picker-label[data-value="5"]::before,
            .ql-picker-item[data-value="5"]::before {
                content: "Heading 5";
            }

            .ql-picker-label[data-value="6"]::before {
                content: "Heading 6";
            }

            .ql-picker-item {
                &[data-value="6"]::before {
                    content: "Heading 6";
                }

                &[data-value="1"]::before {
                    font-size: 2em;
                }

                &[data-value="2"]::before {
                    font-size: 1.5em;
                }

                &[data-value="3"]::before {
                    font-size: 1.17em;
                }

                &[data-value="4"]::before {
                    font-size: 1em;
                }

                &[data-value="5"]::before {
                    font-size: 0.83em;
                }

                &[data-value="6"]::before {
                    font-size: 0.67em;
                }
            }
        }

        &.ql-font {
            width: 108px;

            .ql-picker-label::before,
            .ql-picker-item::before {
                content: "Sans Serif";
            }

            .ql-picker-label[data-value="serif"]::before,
            .ql-picker-item[data-value="serif"]::before {
                content: "Serif";
            }

            .ql-picker-label[data-value="monospace"]::before {
                content: "Monospace";
            }

            .ql-picker-item {
                &[data-value="monospace"]::before {
                    content: "Monospace";
                }

                &[data-value="serif"]::before {
                    font-family: Georgia, Times New Roman, serif;
                }

                &[data-value="monospace"]::before {
                    font-family: Monaco, Courier New, monospace;
                }
            }
        }

        &.ql-size {
            width: 98px;

            .ql-picker-label::before,
            .ql-picker-item::before {
                content: "Normal";
            }

            .ql-picker-label[data-value="small"]::before,
            .ql-picker-item[data-value="small"]::before {
                content: "Small";
            }

            .ql-picker-label[data-value="large"]::before,
            .ql-picker-item[data-value="large"]::before {
                content: "Large";
            }

            .ql-picker-label[data-value="huge"]::before {
                content: "Huge";
            }

            .ql-picker-item {
                &[data-value="huge"]::before {
                    content: "Huge";
                }

                &[data-value="small"]::before {
                    font-size: 10px;
                }

                &[data-value="large"]::before {
                    font-size: 18px;
                }

                &[data-value="huge"]::before {
                    font-size: 32px;
                }
            }
        }
    }

    .ql-color-picker {
        &.ql-background .ql-picker-item {
            background-color: #fff;
        }

        &.ql-color .ql-picker-item {
            background-color: #000;
        }
    }
}

/* .ql-snow.ql-toolbar button:first-child,
  .ql-snow .ql-toolbar button:first-child {
      border-radius: 3px 0 0 3px;
  } */

@media (pointer: coarse) {
    .ql-snow {
        &.ql-toolbar button:hover:not(.ql-active),
        .ql-toolbar button:hover:not(.ql-active) {
            color: #444;
        }

        &.ql-toolbar button:hover:not(.ql-active) .ql-fill,
        .ql-toolbar button:hover:not(.ql-active) .ql-fill,
        &.ql-toolbar button:hover:not(.ql-active) .ql-stroke.ql-fill,
        .ql-toolbar button:hover:not(.ql-active) .ql-stroke.ql-fill {
            fill: #444;
        }

        &.ql-toolbar button:hover:not(.ql-active) .ql-stroke,
        .ql-toolbar button:hover:not(.ql-active) .ql-stroke,
        &.ql-toolbar button:hover:not(.ql-active) .ql-stroke-miter,
        .ql-toolbar button:hover:not(.ql-active) .ql-stroke-miter {
            stroke: #444;
        }
    }
}

.ql-toolbar.ql-snow {
    border: 1px solid #e7eaf3;
    box-sizing: border-box;
    font-family: "Helvetica Neue", "Helvetica", "Arial", sans-serif;
    padding: 8px;
    border-radius: 7px 7px 0 0;

    .ql-formats {
        margin-inline-end: 25px;
    }

    .ql-picker-label {
        border: 1px solid #eff0f6;
        background: #f8f8ff;
        height: 35px;
        line-height: 35px;
        padding: 0 10px;
    }

    .ql-picker-options {
        border: 1px solid transparent;
        box-shadow: rgba(0, 0, 0, 0.2) 0 2px 8px;
    }

    .ql-picker.ql-expanded {
        .ql-picker-label,
        .ql-picker-options {
            border-color: #e7eaf3;
        }
    }

    .ql-color-picker .ql-picker-item {
        &.ql-selected,
        &:hover {
            border-color: #000;
        }
    }

    + .ql-container.ql-snow {
        border-block-start: 0px;
    }
}

.ql-snow {
    .ql-tooltip {
        background-color: #fff;
        border: 1px solid #e7eaf3;
        box-shadow: 0px 0px 5px #ddd;
        color: #444;
        padding: 5px 12px;
        white-space: nowrap;

        &::before {
            content: "Visit URL:";
            line-height: 26px;
            margin-inline-end: 8px;
        }

        input[type="text"] {
            display: none;
            border: 1px solid #e7eaf3;
            font-size: 13px;
            height: 26px;
            margin: 0px;
            padding: 3px 5px;
            width: 170px;
        }

        a {
            &.ql-preview {
                display: inline-block;
                max-width: 200px;
                overflow-x: hidden;
                text-overflow: ellipsis;
                vertical-align: top;
            }

            &.ql-action::after {
                border-inline-end: 1px solid #e7eaf3;
                content: "Edit";
                margin-inline-start: 16px;
                padding-inline-end: 8px;
            }

            &.ql-remove::before {
                content: "Remove";
                margin-inline-start: 8px;
            }

            line-height: 26px;
        }

        &.ql-editing {
            a {
                &.ql-preview,
                &.ql-remove {
                    display: none;
                }
            }

            input[type="text"] {
                display: inline-block;
            }

            a.ql-action::after {
                border-inline-end: 0px;
                content: "Save";
                padding-inline-end: 0px;
            }
        }

        &[data-mode="link"]::before {
            content: "Enter link:";
        }

        &[data-mode="formula"]::before {
            content: "Enter formula:";
        }

        &[data-mode="video"]::before {
            content: "Enter video:";
        }
    }

    a {
        color: $primary-1;
    }
}

.ql-container.ql-snow {
    border: 1px solid #e7eaf3;
}

.ql-toolbar.ql-snow .ql-picker-label:focus {
    outline: 0;
}
