/* =====================================================================================================================
// RangeSlider */

.irs {
    position: relative;
    display: block;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.irs-line {
    position: relative;
    display: block;
    overflow: hidden;
    outline: none !important;
}

.irs-line-left,
.irs-line-mid,
.irs-line-right {
    position: absolute;
    display: block;
    inset-block-start: 0;
}

.irs-line-left {
    inset-inline-start: 0;
    width: 11%;
}

.irs-line-mid {
    inset-inline-start: 9%;
    width: 82%;
}

.irs-line-right {
    inset-inline-end: 0;
    width: 11%;
}

.irs-bar {
    position: absolute;
    display: block;
    inset-inline-start: 0;
    width: 0;
}

.irs-bar-edge {
    position: absolute;
    display: block;
    inset-block-start: 0;
    inset-inline-start: 0;
}

.irs-shadow {
    position: absolute;
    display: none;
    inset-inline-start: 0;
    width: 0;
}

.irs-slider {
    position: absolute;
    display: block;
    cursor: default;
    z-index: 1;

    &.type_last {
        z-index: 2;
    }
}

.irs-min {
    position: absolute;
    display: block;
    inset-inline-start: 0;
    cursor: default;
}

.irs-max {
    position: absolute;
    display: block;
    inset-inline-end: 0;
    cursor: default;
}

.irs-from,
.irs-to,
.irs-single {
    position: absolute;
    display: block;
    inset-block-start: 0;
    inset-inline-start: 0;
    cursor: default;
    white-space: nowrap;
}

.irs-grid {
    position: absolute;
    display: none;
    inset-block-end: 0;
    inset-inline-start: 0;
    width: 100%;
    height: 20px;
}

.irs-with-grid .irs-grid {
    display: block;
}

.irs-grid-pol {
    position: absolute;
    inset-block-start: 0;
    inset-inline-start: 0;
    width: 1px;
    height: 8px;
    background: #000;

    &.small {
        height: 4px;
    }
}

.irs-grid-text {
    position: absolute;
    inset-block-end: 0;
    inset-inline-start: 0;
    white-space: nowrap;
    text-align: center;
    font-size: 9px;
    line-height: 9px;
    padding: 0 3px;
    color: #000;
}

.irs-disable-mask {
    position: absolute;
    display: block;
    inset-block-start: 0;
    inset-inline-start: -1%;
    width: 102%;
    height: 100%;
    cursor: default;
    background: rgba(0, 0, 0, 0);
    z-index: 2;
}

.lt-ie9 .irs-disable-mask {
    background: #000;
    filter: alpha(opacity = 0);
    cursor: not-allowed;
}

.irs-disabled {
    opacity: 0.4;
}

.irs-hidden-input {
    position: absolute !important;
    display: block !important;
    inset-block-start: 0 !important;
    inset-inline-start: 0 !important;
    width: 0 !important;
    height: 0 !important;
    font-size: 0 !important;
    line-height: 0 !important;
    padding: 0 !important;
    margin: 0 !important;
    overflow: hidden;
    outline: none !important;
    z-index: -9999 !important;
    background: none !important;
    border-style: solid !important;
    border-color: transparent !important;
}
