    }

    .fr-toolbar .fr-command.fr-btn.fr-dropdown.fr-active,
    .fr-popup .fr-command.fr-btn.fr-dropdown.fr-active,
    .fr-modal .fr-command.fr-btn.fr-dropdown.fr-active {
        background-color: $transparent-body !important;
    }

    .fr-command.fr-btn+.fr-dropdown-menu .fr-dropdown-wrapper .fr-dropdown-content ul.fr-dropdown-list li a svg path,
    .fr-box a.fr-floating-btn svg,
    .fr-box a.fr-floating-btn:hover svg {
        fill: $text-color;
    }

    .fr-view table th,
    .fr-view table tfoot td,
    .fr-view table.fr-alternate-rows tbody tr:nth-child(2n) {
        background-color: $transparent-theme;
    }

    .fr-view table td,
    .fr-view table th {
        border-color: $transparent-border;
    }

    .fr-box a.fr-floating-btn,
    .fr-box a.fr-floating-btn:hover {
        background-color: $transparent-body;
        color: $text-color;
    }

    .fr-popup .fr-video-upload-layer:hover {
        background-color: $transparent-body;
    }

    .fr-toolbar .fr-command.fr-btn,
    .fr-popup .fr-command.fr-btn,
    .fr-modal .fr-command.fr-btn,
    .fr-desktop .fr-command.fr-selected:not(.fr-table-cell),
    .fr-desktop .fr-command:active {
        color: $text-color;
    }

    .fr-desktop .fr-command:hover,
    .fr-desktop .fr-command:focus,
    .fr-desktop .fr-command.fr-btn-hover,
    .fr-desktop .fr-command.fr-expanded {
        color: $text-color;
    }

    .fr-popup .fr-file-upload-layer:hover,
    .fr-popup .fr-image-upload-layer:hover {
        background-color: $transparent-body;
    }

    .fr-file-size,
    .fr-file-date,
    #fr-logo>span,
    .fr-box .fr-counter {
        color: $white-8;
    }

    .fr-markdown-editor,
    textarea.fr-code {
        background-color: $transparent-theme !important;
        color: $text-color;
    }

    .gutter-horizontal {
        background-color: $transparent-border;
    }

    .e-resize-handler {
        color: $transparent-border;
    }

    .fr-modal .fr-modal-wrapper .fr-modal-head,
    .fr-modal .fr-modal-wrapper {
        background-color: $transparent-body;
    }

    .fr-modal {
        color: $text-color;
    }

    .fr-modal .fr-modal-wrapper .fr-modal-body .fr-help-modal table tbody td:first-child {
        color: $text-color;
    }

    .fr-modal .fr-modal-wrapper .fr-modal-body .fr-help-modal table tbody tr,
    .fr-modal .fr-modal-wrapper .fr-modal-head {
        border-block-end-color: $transparent-border;
    }

    .fr-toolbar .fr-command.fr-btn.fr-dropdown:after,
    .fr-popup .fr-command.fr-btn.fr-dropdown:after,
    .fr-modal .fr-command.fr-btn.fr-dropdown:after {
        border-block-start: 4px solid $transparent-border;
    }

    .fr-toolbar .fr-btn-grp .fr-command.fr-btn.fr-active:not(.fr-dropdown) svg path,
    .fr-toolbar .fr-more-toolbar .fr-command.fr-btn.fr-active:not(.fr-dropdown) svg path,
    .fr-toolbar .fr-buttons:not(.fr-tabs) .fr-command.fr-btn.fr-active:not(.fr-dropdown) svg path,
    .fr-popup .fr-btn-grp .fr-command.fr-btn.fr-active:not(.fr-dropdown) svg path,
    .fr-popup .fr-more-toolbar .fr-command.fr-btn.fr-active:not(.fr-dropdown) svg path,
    .fr-popup .fr-buttons:not(.fr-tabs) .fr-command.fr-btn.fr-active:not(.fr-dropdown) svg path,
    .fr-modal .fr-btn-grp .fr-command.fr-btn.fr-active:not(.fr-dropdown) svg path,
    .fr-modal .fr-more-toolbar .fr-command.fr-btn.fr-active:not(.fr-dropdown) svg path,
    .fr-modal .fr-buttons:not(.fr-tabs) .fr-command.fr-btn.fr-active:not(.fr-dropdown) svg path {
        fill: $text-color !important;
    }

    .fr-popup .fr-action-buttons button.fr-command:hover,
    .fr-popup .fr-action-buttons button.fr-command:focus {
        color: $text-color;
    }

    .fr-popup .fr-checkbox input:not(:checked)+span {
        border: solid 2px $transparent-border;
    }

    #fr-logo:hover>span,
    #fr-logo:focus>span,
    .fr-trim-button {
        color: $text-color !important;
    }

}

.fr-toolbar .fr-float-left {
    float: $float-left !important;

}

.fr-toolbar .fr-float-right {
    float: $float-right !important;
}

